.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  font-size: 22px;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 22px;
  color: $main-text-color;
  overflow: auto;

  &__phone {
    color: inherit;
    text-decoration: none;
    display: block;
    line-height: 27px;
    font-size: 16px;
    margin-bottom: 16px;

    @include hover(color, #6f6e6e);
  }

  &__book-ride {
    color: inherit;
    text-decoration: none;
    display: block;
    line-height: 16px;
    font-size: 12px;
    padding-bottom: 8px;
    border-bottom: 1px solid $main-text-color;
    width: fit-content;

    @include hover(color, #6f6e6e);
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__top {
    margin-bottom: 24px;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #c2c2c2;
  }
}
